.container {
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.wrapper {
    overflow: hidden;
    position: relative;
    width: 450px;
    max-width: 100%;
    margin: auto;
    box-shadow: 0 10px 10px rgba(0,0,0,0.25), 0 5px 10px rgba(0,0,0,0.22);
    border-radius: 10px;
    height: 98%;
    box-sizing: border-box;
}


.search-btn {
    background: orange;
    outline: none;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: white;
    border-radius: 100%;
    border: none;
    box-shadow: 1px 3px 5px rgba(228, 120, 51, 1);
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 10px;
    transition: 0.2s;
}

.search-btn:hover {
    cursor: pointer;
}
.search-btn:active {
    box-shadow: none!important;
}


@keyframes toTop {
    0% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(0px);
    }
}

@keyframes toDown {
    0% {
        transform: translateY(0px);
    }
    100% {
        transform: translateY(700px);
    }
}

@keyframes iconAnimate {
    0% {
        transform: translateY(-5px);
    }
    50% {
        transform: translateY(5px);
    }
    100% {
        transform: translateY(-5px);
    }
}

.icon-animate {
    animation: 2s iconAnimate infinite;
}
.toTop {
    animation: 0.5s toTop cubic-bezier(0.075, 0.82, 0.165, 1);
}
.toDown {
    animation: 0.5s toDown cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
}

/* HEADER */
.header {
    animation: 0.5s toDown cubic-bezier(0.075, 0.82, 0.165, 1);
}
.header-wrapper {
    border-radius: 0 0 40% 40%;
    width: 100%;
    height: 370px!important;
    overflow: hidden;
    position: relative;
}
.header-img {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
}
.header-content {
    padding: 20px 30px;
    color: white;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}
.header-content h1 {
    font-size: 90px;
    margin: 0px;
}
h1 sup {
    font-size: 0.5em!important;
}
.location-text {
    font-size: 2em;
    text-shadow: 1px 1px 5px rgba(0,0,0,0.5);
}

/* END HEADER */


/* DAILY */
.wrap-daily {
    max-width: 100%;
    width: 100%;
    overflow: auto;
    display: flex;
}

::-webkit-scrollbar {
    display: none!important;
}

.wrap-daily .card-info {
    /* margin: 0px 5px; */
    min-width: 100px;
    padding: 8px 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    /* box-shadow: 1px 4px 5px rgba(0,0,0,0.3); */
}
/* END DAILY */



/* LOADING */
.wrap-loading {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: dodgerblue;
    z-index: 2000;
    color: white;
}
/* END LOADING */



/* SEARCH MODAL */
.google-places-autocomplete__input {
    width: 100%!important;
    border: 1px solid dodgerblue!important;
    border-radius: 0px!important;
}
.wrap-search {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transition: 0.3s;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    z-index: 2000;
    box-sizing: border-box;
}

.wrap-search i {
    color: red;
    border: 1px solid red;
    border-radius: 100%;
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
}
.wrap-search i:hover {
    cursor: pointer;
}
.wrap-search input {
    box-sizing: border-box;
    padding: 4px 10px;
    border: 1px solid grey;
    border-radius: 5px;
    width: 90%;
    font-size: 19px;
}
/* END MODAL */
